@use "../ui//variables.scss" as v;


nav {
  background: rgba(125, 125, 125, 0.4);
  width: max-content;
  display: block;
  padding: 0.7rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 2rem;
  display: flex;
  gap: 0.8rem;
  border-radius: 3rem;
  backdrop-filter: blur(15px);

  a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: v.$blanco;
    font-size: 1.1rem;

    &:hover {
      background: rgba(0 0 0 / 0.5);
    }

    &.active {
      background: v.$color-primary-2;
      color: v.$blanco;
    }
  }
}