@use "../ui/variables.scss" as v;

.container.contact__container {
  width: 58%;
  display: grid;
  grid-template-columns: 30% 58%;
  gap: 12%;
}

.contact__options {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

.contact__option {
  background: v.$color-primary-2;
  padding: 1.2rem;
  border-radius: 1.2rem;
  text-align: center;
  border: 1px solid transparent;

  a {
    color: v.$blanco;
    border-bottom: 1px solid white;

    &:hover {
      color: v.$color-primary-2;
    }
  }
}

.contact__option-icon {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

form {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

input,
textarea {
  width: 100%;
  padding: 1.5rem;
  border-radius: 0.5rem;
  background: transparent;
  border: 2px solid v.$color-primary;
  color: v.$blanco;
  resize: none;
  font-size: 1.2rem;
}

input::placeholder {
  color: v.$blanco;
}

// Media Queries (MD)
@media screen and (max-width: 1024px) {
  .container.contact__container {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
}

// Media Queries (SM)
@media screen and (max-width: 600px) {
  .container.contact__container {
    width: v.$ctw-sm;
  }
}
