@use "../ui/variables.scss" as v;

.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}

.about__me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(
    45deg,
    transparent,
    v.$color-primary,
    transparent
  );

  display: grid;
  place-items: center;
}

.about__me-image {
  border-radius: 2rem;
  overflow: hidden;
  transform: rotate(10deg);
  transition: v.$transition;
  height: 100%;

  &:hover {
    transform: rotate(0deg);
  }
}

.about__content {
  display: flex;  
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 15px;

  p {
    margin: 2rem 0 2.6rem;
  }
}

.about__cards {
  display: flex;  
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 15px;

}

.about__card {
  background: v.$color-bg;
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  width: 100%;
}



// Media Queries (MD)
@media screen and (max-width: 1024px) {
  .about__container {
    grid-template-columns: 1fr;
    gap: 0;
  }

  .about__me {
    width: 50%;
    margin: 2rem auto 4rem;
  }

  .about__content {
    p {
      margin: 1rem 0 1.5rem;
    }
  }
}

// Media Queries (SM)
@media screen and (max-width: 600px) {
  .about__me {
    width: 65%;
    margin: 0 auto 3rem;
  }

  .about__cards {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .about__content {
    text-align: center;

    p {
      margin: 1.5rem 0;
    }
  }
}
