@use "./variables.scss" as v;

@import url("https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;500;700&display=swap");

* {
  border: 0;
  margin: 0;
  padding: 0;
  outline: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  color: v.$blanco;
  line-height: 1.7;
  font-family: Ubuntu;
  background: linear-gradient(
      180deg,
      rgba(12, 0, 20, 0.95) 0%,
      rgba(75, 0, 130, 0.95) 100%
    ),
    url(../../assets/background.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.container {
  width: v.$ctw-lg;
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-bottom: 100px;
  h2,
  h5 {
    text-align: center;
    color: v.$blanco;
  }

  h2 {
    color: v.$color-primary;
    margin-bottom: 3rem;
  }
}

.text-light {
  color: v.$color-primary;
}

a {
  color: v.$color-primary;
  transition: all 0.2s ease-in-out;

  &:hover {
    color: v.$blanco;
  }
}

.btn {
  width: max-content;
  display: inline-block;
  color: v.$blanco;
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid v.$color-primary;
  transition: v.$transition;

  display: flex;
  align-items: center;

  &:hover {
    background: v.$blanco;
    color: v.$color-bg-2;
    border-color: transparent;
  }
}

.btn-primary {
  background: v.$color-primary;
  color: v.$color-bg;
}

img {
  height: 100%;
  width: 100%;
}

// Media Queries (MD)
@media screen and (max-width: 1024px) {
  .container {
    width: v.$ctw-md;
  }

  section {
    margin-top: 6rem;
  }
}

// Media Queries (SM)
@media screen and (max-width: 600px) {
  .container {
    width: v.$ctw-sm;
  }

  section {
    margin-top: 2rem;
  }
}
