@use "./variables.scss" as v;

footer {
  background: gray;
  padding: 3rem 0;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;

  .footer__logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
  }

  .permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
  }

  a {
    color: v.$color-bg;
  }

  .footer__social-media {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;

    a {
      background: v.$color-bg;
      color: v.$blanco;
      padding: 0.8rem;
      border-radius: 0.7rem;
      display: flex;
      border: 1px solid transparent;

      &:hover {
        background: transparent;
        color: v.$color-bg;
        border-color: v.$color-bg;
      }
    }
  }

  .footer__copyright {
    margin-bottom: 4rem;
    columns: v.$color-bg-2;
  }
}

// Media Queries (MD)
@media screen and (max-width: 1024px) {
  .about__container {
    grid-template-columns: 1fr;
    gap: 0;
  }

  .about__me {
    width: 50%;
    margin: 2rem auto 4rem;
  }

  .about__content {
    p {
      margin: 1rem 0 1.5rem;
    }
  }
}

// Media Queries (SM)
@media screen and (max-width: 600px) {
  .permalinks {
    flex-direction: column;
    gap: 1.5rem;
  }

  .footer__social-media {
    margin-bottom: 2rem;
  }
}
