@use "../ui/variables.scss" as v;

header {
  min-height: 100vh;
  padding: 7rem;
  overflow: hidden;
  margin-bottom: 60px;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
  width: 100%;

  img {
    width: 50%;
    margin-top: 50px;
  }
}

.header__buttons {
  margin-top: 2rem;
}

// Header Buttons
.cta {
  display: flex;
  margin-top: 1rem;
  gap: 1.2rem;
  justify-content: center;
}

// Social Media Buttons
.header__social-media {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.8rem;
}

// Media Queries (MD)
@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }
}

// Media Queries (SM)
@media screen and (max-width: 600px) {
  header {
    height: 100vh;
    padding: 7rem 0 7rem;
  }

  .header__container {
    img {
      width: 100%;
      margin-top: 20px;
    }
  }

  .header__social-media {
    display: none;
  }
}
