@use "../ui/variables.scss" as v;

.skills__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
}

.skills__container > div {
  padding: 2.4rem 5rem;
  border-radius: 2rem;
  border: 1px solid v.$color-primary-2;
  background: transparent;
  transition: v.$transition;

  &:hover {
    background: v.$color-primary-2;
  }
  h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: v.$color-primary;
  }
}

.skills__content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 2rem;
}

.skills__details {
  display: flex;
  gap: 1rem;
}

.skills__icon {
  margin-top: 6px;
}

// Media Queries (MD)
@media screen and (max-width: 1024px) {
  .skills__container {
    grid-template-columns: 1fr;
  }

  .skills__container > div {
    width: 80%;
    margin: 0 auto;
    padding: 2rem;
  }

  .skills__content {
    padding: 1rem;
  }
}

// Media Queries (SM)
@media screen and (max-width: 600px) {
  .skills__container {
    gap: 1rem;
  }
  .skills__container > div {
    width: 100%;
    padding: 2rem 1rem;
  }
}
